import React from 'react'
//import clsx from 'clsx'

import i18n from '../../controller/i18n'

import Dialog from '../../components/Dialog'

import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Link from '@material-ui/core/Link'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	Paper: {
		padding: theme.spacing(2)
	},
	Avatar: {
		borderRadius: '50%',
		border: '3px solid #eee'
	}
}));


const md = require('markdown-it')();
i18n.setTranslations(language => require('./i18n/' + language));

export default function DonationPage(props) {
	const classes = useStyles();
	
	const zefau = require('../../images/zefau.jpg');
	const paypal = require('../../images/paypal.webp');
	
	const donationText = ['donation#TextFeatures', 'donation#TextBugs', 'donation#TextFeedback', 'donation#TextSupport', 'donation#TextGreetings'].map(text => {
		return <div key={text.replace('#', '-')} dangerouslySetInnerHTML={{ __html: md.render(i18n.t(text)).replace(/<a /g, '<a class="MuiTypography-root MuiLink-root MuiLink-underlineHover MuiTypography-colorPrimary" ') }} />
	});
	
	return (
		<Dialog
			fullWidth
			maxWidth="md"
			open={props.open}
			onClose={() => props.navigate('home')}
			
			disableBackdropClick={false}
			disableEscapeKeyDown={false}
			
			header={i18n.t('Support the development of jarvis')}
			actions={
				<Button onClick={() => props.navigate('home')} color="primary">
					{i18n.t('Close')}
				</Button>
			}
			>
			
			<Paper classes={{ root: classes.Paper }}>
				<Grid container>
					<Grid container item xs={3} justify="center">
						<img alt="Zefau" src={zefau} className={classes.Avatar} width="126" height="126" />
					</Grid>
					<Grid item xs={9}>
						<Link target="_blank" href="https://forum.iobroker.net/user/zefau">@Zefau (Profil)</Link> | <Link target="_blank" href="https://zefau.github.io/iobroker/">{i18n.t('Overview of my adapters')}</Link><br />
						<br />
						
						<strong>{i18n.t('donation#TextIntro')}</strong><br />
						{donationText}
						
						<Link target="_blank" href="https://paypal.me/chvoelkel">
							<img alt="Paypal" src={paypal} />
						</Link>
					</Grid>
				</Grid>
			</Paper>
			
		</Dialog>
	);
}
